.modal-page-shadow-background {
  display: none;
  /* turning flex */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 16;
  background-color: var(--color-dark-opacity);

  /* too laggy :( */
  /* backdrop-filter: blur(2px); */
}

.modal-page-wrapper {
  display: none;
  /* turning flex */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 17;
  align-items: center;
  justify-content: center;
}

.modal-page-wrapper .sizeable-content {
  position: relative;
  width: auto;
  height: auto;
  min-width: 30px;
  min-height: 40px;
  font-size: 1rem;
  background-color: var(--color-1);
  border-radius: var(--border-radius);
  padding: 20px;
}

.modal-page-wrapper .sizeable-content h1 {
  font-size: 1.6rem;
}

.modal-page-wrapper .sizeable-content .exit {
  position: absolute;
  top: 22px;
  right: 20px;
  color: var(--font-2);
  font-size: 1.4rem;
  /* cursor: pointer; */
  background-color: transparent;
  border: none;
  outline: none;
  transition: var(--transition);
  z-index: 22;
}

.modal-page-wrapper .sizeable-content .exit:hover {
  color: var(--font-1);
}

.modal-page-wrapper .sizeable-content {
  color: var(--font-3);
}

.modal-page-shadow-background.tab-like {
  background-color: transparent; /* No shadow for tab-like view */
  backdrop-filter: none;
  pointer-events: none; /* Allow clicking through to the navbar */
  top: 0; /* Fill entire viewport */
  left: 0;
  height: 100%;
  width: 100%;
}

.modal-page-wrapper.tab-like {
  align-items: flex-start;
  justify-content: flex-start;
  background-color: transparent;
  pointer-events: none; /* Base wrapper doesn't capture clicks */
  top: 0; /* Fill entire viewport */
  left: 0;
  height: 100%;
  width: 100%;
}

.modal-page-wrapper.tab-like > * {
  pointer-events: auto; /* Children can receive clicks */
}
